.now_container{
    height: 100%;
    display: flex;

}

.now_loader{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

.nowPlaying_text{
    position:absolute;
    top:40px;
    left:20px;
    padding-left: 30px;
    padding-top: 10px;
    font-size: 28px;

}

.now_movies {
    display: grid;
    grid-template-columns: repeat(5, minmax(350px, 1fr));
    grid-gap: 0px;
    padding: 40px;
    width: 100%;
    padding-top: 150px;
}

@media screen and (max-width:1090px){
.now_movies{
    grid-template-columns: 1fr;
    width: 100%;
}    
}