.nav{
    width: 1860px;;
    z-index : 1;
    position: fixed;
    top: initial;
    left: initial;
    display:inline-block;
    flex-direction:initial;
    background-color: white;
    padding: 25px;
    padding-left: 4px;

    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.23),
                0 8px 16px -8px rgba(0, 0, 0, 0.3)
                0 -6px 16px -6px rgba(0, 0, 0, 0.25);
                border-radius: 5px;
}


.nav.a{
    text-decoration: none;
    color:#0008fc;
    text-transform: uppercase;
    font-size: 12px;
    text-align:justify;
    font-weight: 600;
}

.nav a:not(:last-child){
    margin-bottom: 20px;
}

.navMenu{
    color: black;
    margin:40px;
    text-decoration:none;
}
.TMDB_LOGO{
    font-family:'Courier New', Courier, monospace;
    font-size: 15px;
    background-color:dimgrey;
    padding: 20px;
    color: white;

}
.TMDB_img{
    padding-left: 10px;
    width: 100px;
}